.loading{
    width: 100vw;
    height: 100vh;
    background-color: rgb(255, 255, 255);
    display: grid;
    place-items: center;
    max-width: 100%;
    position: fixed;
}
.loading>div{
    width: 10vmax;
    height: 10vmax;
    border-bottom: 5px solid crimson;
    border-radius: 50%;
    animation: loadingRotate 800ms linear  infinite;
}

@keyframes loadingRotate {
    to{
        transform: rotateZ(-360deg);
    }
    
}